import { list } from 'postcss';
import {
  getData
} from './getData.js';

const renderCalc = () => {

  const blockCars = document.querySelector('.select__cars');

  const classesList = document.querySelector('.select__classes-list'); //СПИСОК КЛАССОВ
  const carsList = document.querySelector('.select__cars-list'); //СПИСОК МАШИН
  const startListItem = document.querySelector('.start');

  const classesListItem = document.querySelectorAll('.select__classes-list-item'); //ЭЛЕМЕНТЫ СПИСКА КЛАССОВ
  const selectClassesBtn = document.querySelector('.select__classes-button'); //кнопка разворачивания списка классов
  const selectCarsBtn = document.querySelector('.select__cars-button'); //кнопка разворачивания списка машин

  let dayPrice = document.querySelector('.calculator__fields-day-price--span');//ПОЛЕ СУММЫ ЗА 1 ДЕНЬ
  let guarantiePrice = document.querySelector('.calculator__fields-guarantie--span');//ПОЛЕ СУММЫ ЗАЛОГА

  const inputClassValue = document.querySelector('.select__input-class'); //ИНПУТ СОХРАНЕНИЯ КЛАССА
  const inputPriceValue = document.querySelector('.select__input-price'); //ИНПУТ СОХРАНЕНИЯ ЦЕНЫ 
  const inputGuarantieValue = document.querySelector('.select__input-guarantie'); //ИНПУТ СОХРАНЕНИЯ ЗАЛОГА
  const days = document.querySelector('.day-input'); //ИНПУТ ДНЕЙ

  const totalBtn = document.querySelector('.calculator__button'); //КНОПКА РАСЧЕТА
  const totalField = document.querySelector('.calculator__fields-total--span'); // ИТОГОВАЯ СУММА
  

  const resetInputs = () => {
    inputClassValue.value = '';
    inputPriceValue.value = '';
    inputGuarantieValue.value = '';
    totalField.textContent = '';
    dayPrice.textContent = '';
    guarantiePrice.textContent = '';
    days.value = 1;
  };


  /* ФУНЦКИЯ РЕНДЕР СПИСКА КЛАССОВ */
  const renderClassesList = (classes, classesNames) => {
    Array.from(classes).forEach((classItem, index) => {
      const classOption = document.createElement('li');
      classOption.setAttribute('data-class', classItem);
      classOption.classList.add('select__classes-list-item');
        classOption.innerHTML = `${Array.from(classesNames)[index]}`;
      classesList.append(classOption);
    });
  };    
  
  selectClassesBtn.addEventListener('click', (e) => {
    e.stopPropagation();
    classesList.classList.toggle('select__classes-list--visible');
    classesList.classList.add('select__classes-button--active');
    carsList.classList.remove('select__cars-list--visible');
    carsList.classList.remove('select__cars-button--active');
    const classesListItem = document.querySelectorAll('.select__classes-list-item'); //ЭЛЕМЕНТЫ СПИСКА КЛАССОВ
    resetInputs();
    classesListItem.forEach(listItem => {
      listItem.addEventListener('click', (e) => {
        selectClassesBtn.innerText = e.target.innerText;
        selectCarsBtn.innerText = startListItem.innerText;
        resetInputs();
        selectClassesBtn.focus();
        inputClassValue.value = e.target.dataset.class;
        classesList.classList.remove('select__classes-list--visible');
        if (inputClassValue.value) {
          blockCars.classList.add('select__cars--visible');
        } else {
          blockCars.classList.remove('select__cars--visible');
          resetInputs();
        }
      });
    });
  });

  const renderCarsList = (arr) => {
    let carArr = [];
      carsList.innerHTML = '';
      carsList.append(startListItem);
      carArr = arr.filter(item => item.rate == inputClassValue.value);
      console.log(carArr); //отфильтрованный список
      carArr.forEach(car => {
        const carOption = document.createElement('li');
        carOption.classList.add('select__cars-list-item');
        carOption.setAttribute('data-price', car.price);
        carOption.setAttribute('data-guarantie', car.guarantie);
        carOption.innerHTML = `${car.name}, ${car.year} года`;
        carsList.append(carOption);
        
      });

      selectCarsBtn.addEventListener('click', (e) => {
        carsList.classList.add('select__cars-list--visible');
        carsList.classList.add('select__cars-button--active');
        
      const carsListItem = document.querySelectorAll('.select__cars-list-item'); //ЭЛЕМЕНТЫ СПИСКА МАШИН
      carsListItem.forEach(carItem => {
        carItem.addEventListener('click', (e) => {
          e.stopPropagation();
          selectCarsBtn.innerText = e.target.innerText;
          selectCarsBtn.focus();
          dayPrice.textContent = `${e.target.dataset.price} ₽`;
          guarantiePrice.textContent = `${e.target.dataset.guarantie} ₽`;
          inputPriceValue.value = e.target.dataset.price;
          inputGuarantieValue.value = e.target.dataset.guarantie;
          carsList.classList.remove('select__cars-list--visible');
          //calculate();
        });
      });
    });
  };

  document.addEventListener('click', (e) => {
    if(e.target !== selectClassesBtn) {
      classesList.classList.remove('select__classes-list--visible');
      classesList.classList.remove('select__classes-button--active');
    }
    if(e.target !== selectCarsBtn) {
      carsList.classList.remove('select__cars-list--visible');
      carsList.classList.remove('select__cars-button--active');
    }
  });

  days.addEventListener('input', () => {

    if (days.value < 3 && inputPriceValue.value !== '') {
      dayPrice.textContent = `${inputPriceValue.value} ₽`;
    }
    if ((days.value > 3 && days.value <= 6) && inputClassValue.value !== 'business' && inputPriceValue.value !== '') {
      dayPrice.textContent = `${+inputPriceValue.value - 100} ₽`;
    }
    if ((days.value > 6 && days.value <= 14) && inputClassValue.value !== 'business' && inputPriceValue.value !== '') {
      dayPrice.textContent = `${+inputPriceValue.value - 200} ₽`;
    }
    if ((days.value > 14) && inputClassValue.value !== 'business' && inputPriceValue.value !== '') {
      dayPrice.textContent = `${+inputPriceValue.value - 300} ₽`;
    }

    if ((days.value > 3 && days.value <= 6) && inputClassValue.value === 'business') {
      dayPrice.textContent = `${+inputPriceValue.value - 200} ₽`;
    }
    if ((days.value > 6 && days.value <= 14) && inputClassValue.value === 'business') {
      dayPrice.textContent = `${+inputPriceValue.value - 400} ₽`;
    }
    if ((days.value > 14) && inputClassValue.value === 'business') {
      dayPrice.textContent = `${+inputPriceValue.value - 600} ₽`;
    }

  });
  const calculate = () => {
    let dayPriceTotal = (dayPrice.textContent.substring(0, dayPrice.textContent.length - 1)).trim();
    let guarantiePriceTotal = (guarantiePrice.textContent.substring(0, guarantiePrice.textContent.length - 1)).trim();
    console.log(dayPrice.textContent);
    console.log(guarantiePrice.textContent);

    if (dayPrice.textContent !== '' && guarantiePrice.textContent !== '' && days.value !== '') {
      totalField.textContent = `${+guarantiePriceTotal + +dayPriceTotal * +days.value} ₽`;
      console.log(totalField.textContent);
    } else {
      totalField.textContent = '';
      return;
    }
  };
  totalBtn.addEventListener('click', calculate);

  // const renderCarsList = (arr) => {
  //   console.log(arr);
  //   let carArr = [];
  //   classesList.addEventListener('change', () => {
  //     carsList.innerHTML = '';
  //     carsList.append(first);
  //     console.log(classesList.value);
  //     carArr = arr.filter(item => item.rate == classesList.value);
  //     console.log(carArr);
  //     carArr.forEach(car => {
  //       const carOption = document.createElement('option');
  //       carOption.setAttribute('data-guarantie', car.guarantie);
  //       carOption.value = car.price;
  //       carOption.innerHTML = `${car.name}, ${car.year} года`;
  //       carsList.append(carOption);
  //     });
  //     carsList.style.visibility = 'visible';

  //     if(!classesList.value) {
  //       carsList.style.visibility = 'hidden';
  //     }
  //     dayPrice.textContent = '';
  //   });
  //   carsList.addEventListener('change', (e) => {
  //     guarantiePrice.textContent = '';
  //     console.log(carsList.value);
  //     console.log(carsList.textContent);
  //     dayPrice.textContent = carsList.value;
  //     carsList.childNodes.forEach(item => {
  //       guarantiePrice.textContent = item.dataset.guarantie;
  //     });
      
      
  //   });
  // };


  getData('../db/db.json')
    .then(data => {
      const classes = new Set();
      const classesNames = new Set();
      data.forEach(item => {
        classes.add(item.rate);
        classesNames.add(item.rateName);
      });
      renderClassesList(classes, classesNames);
      selectClassesBtn.nextElementSibling.addEventListener('click', (e) => {
        renderCarsList(data);
        });
    });
};

export default renderCalc;